body{
  background-color:#ccddff
 }

 .app{
  text-align:center;
 }
 .container{
  background-color:black;
  justify-content:center;
  width:50%;
  margin-left:26%;
  margin-top:5%;
  text-align:center;
  padding:20px;
 }
 input[type=text], select {
 width:80%;
 padding:12px 20px;
 margin:30px 0;
 display:inline-block;
 border:1px solid #ccc;
 border-radius:4px;
 box-sizing:border-box;
 }
 button{
  width:15%;
  background-color: pink;
  color:black;
  padding:14px 20px;
  margin:8px 0;
  border:none;
  cursor:pointer;
  font-weight:bold;
 }
 button:hover{
  background-color: green;
 }
 .header{
  width:100%;
  padding:12px 20px;
  margin-left:120px;
  color:black;
  font-size:50px;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  background-color: pink;
  width:60%;
 }
 .List-heading{
  color:rgb(15, 14, 14);
  font-size:50px;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  text-align:left;
  background-color: pink;
  width:60%;
 }
 .listData{
  color:black;
  background-color: white;
  font-size:20px;
  text-align:left;
  padding-left:30px;
  width:53%;
  padding-bottom:2%;
  padding-top:2%;
  border-radius:10px;
 }
 .btn-position{
  position:relative;
  left:14.5%;
  top:-54px;
 }